.body {
  counter-reset: section;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  gap: 1px;
  width: 706px;
  display: flex;
}

.calendar__day {
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
  transition: all .5s;
  position: relative;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

.calendar__day:before {
  counter-increment: section;
  content: counter(section);
  margin-right: -50%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.calendar--start-day-mon :first-child {
  margin-left: 0;
}

.calendar--start-day-tue :first-child {
  margin-left: 101px;
}

.calendar--start-day-wed :first-child {
  margin-left: 202px;
}

.calendar--start-day-thu :first-child {
  margin-left: 303px;
}

.calendar--start-day-fri :first-child {
  margin-left: 404px;
}

.calendar--start-day-sat :first-child {
  margin-left: 505px;
}

.calendar--start-day-sun :first-child {
  margin-left: 606px;
}

.calendar--month-length-28 .calendar__day:nth-child(n+29), .calendar--month-length-29 .calendar__day:nth-child(n+30), .calendar--month-length-30 .calendar__day:nth-child(n+31) {
